<template>
  <div>
    <div class="imitationEchartsTooltip"
      :style="{top:tooltipTop,left:tooltipLeft}"
      v-show="tooltipBlCtrl">
      {{imitationObj.tooltipText}}<br />{{tooltipSon}}
    </div>
    <div class="imitationEcharts">
      <div class="echartsSon"
        v-for="(item,num) in imitationData"
        :key="num">
        <span class="echartsName">
          <span :class="`echartsRankingNum echartsRankingNum${num+1}`">{{num+1}}</span>
          <span class="echartsRankingName">{{item.name}}</span>
        </span>
        <span class="echartsItem">
          <span :class="`echartsDataItem echartsDataItem${num+1}`"
            :style="{'width':item.width}"
            @mousemove="VIPRankingMousemove(num+1, $event)"
            @mouseleave="tooltipBl=false"
            @mouseenter="tooltipBl=true"></span>
          <span class="echartsDefaultItem"></span>
        </span>
        <span :class="`echartsRight echartsRight${num+1}`">{{item.number}}
        </span>
      </div>
    </div>
  </div>
</template>
<script>
/**
 *
 * @param  imitationData:Array,如[{ name: '我是第1条', number: 99, width: '99%' }],
 * @param  imitationObj：obj,暂时只有一个字段：{tooltipText: 'tooltip的头部信息'}
 */
export default {
	props: {
		imitationData: {
			type: Array
		},
		imitationObj: {
			type: Object
		}
	},
	data() {
		return {
			imitationVIPRankingMaxVal: 100,
			tooltipBl: false,
			tooltipIndex: 1,
			tooltipSon: '',
			itemOffsetX: 0,
			winX: 0,
			winY: 0,
			winWidth: window.innerWidth,
			winHeight: window.innerHeight
		};
	},
	methods: {
		VIPRankingMousemove(index, event) {
			this.tooltipSon =
        this.imitationData[index - 1].name +
        ':' +
        this.imitationData[index - 1].number;
			this.winX = event.clientX;
			this.winY = event.clientY;
			this.tooltipIndex = index;
			this.itemOffsetX = event.offsetX;
		}
	},
	computed: {
		tooltipTop: function() {
			if (
				this.tooltipIndex == 9 ||
        this.tooltipIndex == 10 ||
        this.tooltipIndex == 8
			) {
				return this.winY - this.winHeight * 0.07 + 'px';
			} else {
				return this.winY + this.winHeight * 0.03 + 'px';
			}
		},
		tooltipLeft: function() {
			if (this.itemOffsetX > this.winWidth * 0.154) {
				return this.winX - this.winWidth * 0.08 + 'px';
			} else {
				return this.winX + 'px';
			}
		},
		tooltipBlCtrl: function() {
			if (
				parseInt(this.tooltipTop) > this.winHeight * 0.068 &&
        this.tooltipBl
			) {
				return true;
			} else {
				return false;
			}
		}
	}
};
</script>
<style scoped>
/* 模拟echarts样式 */
.imitationEcharts {
  width: 31.2vw;
  height: 33.3vh;
  position: absolute;
  top: 4vh;
  left: 0;
}
/* 每一个数据的样式 */
/* 左侧文字 */
.echartsSon {
  text-align: center;
  font-size: 0.6vw;
  width: 31.2vw;
  height: 3.3vh;
}
.echartsName {
  width: 6.5vw;
  height: 3.2vh;
  text-align: left;
  padding-left: 0.5vw;
  padding-right: 0.5vw;
}
.echartsRankingNum {
  background-color: rgb(40, 179, 252);
  color: #fff;
  width: 2vh;
  height: 2vh;
  display: inline-block;
  text-align: center;
}
.echartsRankingNum1 {
  background-color: rgb(252, 113, 142);
}
.echartsRankingNum2 {
  background-color: rgb(29, 124, 250);
}
.echartsRankingNum3 {
  background-color: rgb(0, 248, 255);
}
.echartsRankingName {
  color: #fff;
  white-space: nowrap;
  width: 3.7vw;
  display: inline-block;
  line-height: 1.2vh;
  overflow-x: hidden;
  text-overflow: ellipsis;
  position: relative;
  top: 0.1vh;
}
/* 中间柱子 */
.echartsItem {
  width: 20.4vw;
  position: relative;
  background-color: #ccc;
  display: inline-block;
}
.echartsDataItem {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 70%;
  background: -webkit-linear-gradient(
    left,
    rgba(38, 48, 64, 0),
    rgba(40, 178, 251, 1)
  ); /* Safari 5.1 - 6.0 */
  background: -o-linear-gradient(
    right,
    rgba(38, 48, 64, 0),
    rgba(40, 178, 251, 1)
  ); /* Opera 11.1 - 12.0 */
  background: -moz-linear-gradient(
    right,
    rgba(38, 48, 64, 0),
    rgba(40, 178, 251, 1)
  ); /* Firefox 3.6 - 15 */
  background: linear-gradient(
    to right,
    rgba(38, 48, 64, 0),
    rgba(40, 178, 251, 1)
  ); /* 标准的语法（必须放在最后） */
  height: 0.6vh;
  border-radius: 0.3vh;
  /* border-bottom-right-radius: 0.3vh; */
  z-index: 2;
  cursor: pointer;
}
.echartsDataItem1 {
  background: -webkit-linear-gradient(
    left,
    rgba(38, 48, 64, 0),
    rgba(252, 113, 142, 1)
  ); /* Safari 5.1 - 6.0 */
  background: -o-linear-gradient(
    right,
    rgba(38, 48, 64, 0),
    rgba(252, 113, 142, 1)
  ); /* Opera 11.1 - 12.0 */
  background: -moz-linear-gradient(
    right,
    rgba(38, 48, 64, 0),
    rgba(252, 113, 142, 1)
  ); /* Firefox 3.6 - 15 */
  background: linear-gradient(
    to right,
    rgba(38, 48, 64, 0),
    rgba(252, 113, 142, 1)
  ); /* 标准的语法（必须放在最后） */
}
.echartsDataItem2 {
  background: -webkit-linear-gradient(
    left,
    rgba(38, 48, 64, 0),
    rgba(29, 124, 249, 1)
  ); /* Safari 5.1 - 6.0 */
  background: -o-linear-gradient(
    right,
    rgba(38, 48, 64, 0),
    rgba(29, 124, 249, 1)
  ); /* Opera 11.1 - 12.0 */
  background: -moz-linear-gradient(
    right,
    rgba(38, 48, 64, 0),
    rgba(29, 124, 249, 1)
  ); /* Firefox 3.6 - 15 */
  background: linear-gradient(
    to right,
    rgba(38, 48, 64, 0),
    rgba(29, 124, 249, 1)
  ); /* 标准的语法（必须放在最后） */
}
.echartsDataItem3 {
  background: -webkit-linear-gradient(
    left,
    rgba(38, 48, 64, 0),
    rgba(0, 247, 254, 1)
  ); /* Safari 5.1 - 6.0 */
  background: -o-linear-gradient(
    right,
    rgba(38, 48, 64, 0),
    rgba(0, 247, 254, 1)
  ); /* Opera 11.1 - 12.0 */
  background: -moz-linear-gradient(
    right,
    rgba(38, 48, 64, 0),
    rgba(0, 247, 254, 1)
  ); /* Firefox 3.6 - 15 */
  background: linear-gradient(
    to right,
    rgba(38, 48, 64, 0),
    rgba(0, 247, 254, 1)
  ); /* 标准的语法（必须放在最后） */
}
.echartsDefaultItem {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: rgb(38, 48, 63);
  height: 0.6vh;
  border-radius: 0.3vh;
  z-index: 1;
}
/* 右侧数字 */
.echartsRight {
  width: 3.5vw;
  display: inline-block;
  color: rgb(40, 179, 252);
}
.echartsRight1 {
  color: rgb(252, 113, 142);
}
.echartsRight2 {
  color: rgb(29, 124, 250);
}
.echartsRight3 {
  color: rgb(0, 248, 255);
}
/* 模拟弹窗 */
.imitationEchartsTooltip {
  background-color: rgba(43, 46, 50, 0.8);
  position: fixed;
  z-index: 11;
  border-radius: 0.6vh;
  /* position: absolute; */
  border-style: solid;
  white-space: nowrap;
  z-index: 9999999;
  transition: left 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s,
    top 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;
  background-color: rgba(50, 50, 50, 0.7);
  border-width: 0px;
  border-color: rgb(51, 51, 51);
  border-radius: 4px;
  color: rgb(255, 255, 255);
  font: 14px/21px "Microsoft YaHei";
  padding: 5px;
  top: 90vh;
  left: 50vw;
  pointer-events: none;
}
</style>
