};
export default {
	name: 'ctrlMenu',
	components: { IEcharts, MimeEcharts },
	data() {
		return {
			srcoll: {
				barColor: 'rgba(149, 160, 182)', //滚动条颜色
				barWidth: 6, //滚动条宽度
				railColor: 'rgba(71, 85, 102)', //导轨颜色
				barMarginRight: -1, //垂直滚动条距离整个容器右侧距离单位（px）
				barOpacityMin: 0.3, //滚动条非激活状态下的透明度
				vBarHeight: '20vh'
			},
			// 今日会员
			todayVIPNum: 0,
			todayVIP: todayVIP,
			// 会员总数
			VIPSum: 0,
			VIPTotal: VIPTotal,
			// 今日订单
			todayOrderNum: 0,
			todayOrder: todayOrder,
			// 订单总数
			orderTotalSum: 0,
			orderTotal: orderTotal,

			VIPGetUp: VIPGetUp,
			VIPConsume: VIPConsume,
			VIPScan: VIPScan,
			loading: true,
			readonly: false,
			WorkOrderListArr: [
				{
					WorkOrderElP: 'Oracle在已保存数据库中选择一数据报表上传... ...',
					WorkOrderDepict: 'Oracle在已经保存... ...',
					WorkOrderTime: '2019/5/30 16:33:33',
					img:
            'data:image/png;base64,',
					status: '处理中'
				},
				{
					WorkOrderElP: 'Oracle在已保存数据库中选择一数据报表上传... ...',
					WorkOrderDepict: 'Oracle在已经保存... ...',
					WorkOrderTime: '2019/5/30 16:33:33',
					img:
            'data:image/png;base64,',
					status: '处理中'
				},
				{
					WorkOrderElP: 'Oracle在已保存数据库中选择一数据报表上传... ...',
					WorkOrderDepict: 'Oracle在已经保存... ...',
					WorkOrderTime: '2019/5/30 16:33:33',
					img:
            'data:image/png;base64,',
					status: '处理中'
				},
				{
					WorkOrderElP: 'Oracle在已保存数据库中选择一数据报表上传... ...',
					WorkOrderDepict: 'Oracle在已经保存... ...',
					WorkOrderTime: '2019/5/30 16:33:33',
					img:
            'data:image/png;base64,',
					status: '处理中'
				}
			],

			// 会员结构分析
			VIPGetUpCenterText: '0%',
			initVIPGetUpSum: 1000,
			initVIPGetUp: data1,
			initVIPGetUpLegendBl: {
				0: true,
				1: true,
				2: true,
				3: true,
				4: true,
				5: true,
				6: true,
				7: true
			},
			// 会员消费分析
			initVIPConsumeText: '0%',
			initVIPConsumeSum: 1000,
			initVIPConsume: data2,
			initVIPConsumeLegendBl: {
				0: true,
				1: true,
				2: true,
				3: true,
				4: true,
				5: true,
				6: true,
				7: true
			},

			// 用户点击查询会员动态
			VIPMovingEnter: false,
			// 用户点击查询工单信息
			VIPTitleEnter: false,
			imitationVIPData: [],
			imitationVIPObj: {
				tooltipText: '会员活跃度Top10排行'
			},
			imitationData: [],
			imitationObj: {
				tooltipText: '仪表盘浏览Top10排行'
			}
		};
	},
	methods: {
		init() {
			this.$get('api/controlPanel').then(res => {
				if (res.code == '000000') {
					var data = res.data;
					// 上方四个值；
					this.todayVIPNum = data.todayMember;
					this.VIPSum = data.sumMember;
					this.todayOrderNum = data.todayOrder;
					this.orderTotalSum = data.sumOrder;
					// 开始处理返回值
					var initVIPConsumeData = res.data.VIPConsume;
					var initVIPGetUpData = res.data.VIPGetup;
					for (let i = 0; i < initVIPConsumeData.length; i++) {
						initVIPConsumeData[i].number = initVIPConsumeData[i].money;
					}
					// 求总数
					var getSumFn = function(arr) {
						var sum = 0;
						for (let i = 0; i < arr.length; i++) {
							sum += parseInt(arr[i].number * 100) / 100;
						}
						return sum;
					};
					// 求echarts中data的值
					var getEchartsDataFn = function(arr) {
						var echartsData = [];
						for (let i = 0; i < arr.length; i++) {
							echartsData.push({
								itemStyle: {
									color: color[i]
								},
								emphasis: {
									show: false
								},
								name: arr[i].name,
								value: arr[i].number
							});
						}
						return echartsData;
					};
					// 页面中data的值：
					var getPageDataFn = function(arr) {
						var getPageData = [];
						for (let i = 0; i < arr.length; i++) {
							var per='';
							if (arr[i].number==0) {
								per='0%';
							}else{
								per=parseInt((arr[i].number / getSumFn(arr)) * 10000) / 100 + '%';
							}
							getPageData.push({
								name: arr[i].name,
								number: arr[i].number,
								color: color[i],
								percentage:per
							});
						}
						return getPageData;
					};
					// 会员消费分析赋值
					// 总数
					this.initVIPConsumeSum = getSumFn(initVIPConsumeData);
					// echarts中data的值
					this.VIPConsume.series[0].data = getEchartsDataFn(initVIPConsumeData);
					// 页面中的模拟值
					this.initVIPConsume = getPageDataFn(initVIPConsumeData);
					// 百分比
					if (initVIPConsumeData[0].number==0) {

						this.initVIPConsumeText ='0%';
					}else{
						this.initVIPConsumeText =
            parseInt(
            	(initVIPConsumeData[0].number / getSumFn(initVIPConsumeData)) *
                10000
            ) /
              100 +
            '%';
					}
					if (initVIPGetUpData) {
						// 会员结构分析赋值
						// echarts中data的值
						this.VIPGetUp.series[0].data = getEchartsDataFn(initVIPGetUpData);
						// 总数
						this.initVIPGetUpSum = getSumFn(initVIPGetUpData);
						// 页面中的模拟值
						this.initVIPGetUp = getPageDataFn(initVIPGetUpData);
						// 百分比
						if (initVIPGetUpData[0].number==0) {
							this.VIPGetUpCenterText ='0%';
						}else{
							this.VIPGetUpCenterText =
              parseInt(
              	(initVIPGetUpData[0].number / getSumFn(initVIPGetUpData)) *
                  10000
              ) /
                100 +
              '%';
						}
					}

					// 求最大值
					var getMaxNum = function(arr) {
						var maxNum = 1;
						for (let i = 0; i < arr.length; i++) {
							if (maxNum < arr[i].number) {
								maxNum = arr[i].number;
							}
						}
						return maxNum;
					};
					// 冒泡排序 从大到小
					var ding = function(arr) {
						var n = arr.length;
						for (var i = 0; i < n - 1; i++) {
							if (arr[i].number == '') {
								arr[i].number = 0;
							}
							for (var j = 0; j < n - 1 - i; j++) {
								if (arr[j].number < arr[j + 1].number) {
									var temp = arr[j];
									arr[j] = arr[j + 1];
									arr[j + 1] = temp;
								}
							}
						}
						return arr;
					};
					// 获取top10排行中百分比的值
					var getPercentage = function(arr) {
						var newArr = arr;
						for (let i = 0; i < newArr.length; i++) {
							if (!newArr[i].name) {
								newArr[i].name = '未命名';
							}
							if (!newArr[i].number) {
								newArr[i].number = 0;
							}
						}
						var maxNumber = getMaxNum(newArr) * 1.1;
						for (let i = 0; i < newArr.length; i++) {
							newArr[i].width =
                Math.floor((newArr[i].number * 10000) / maxNumber) / 100 + '%';
						}
						return newArr;
					};
					var share_data = ding(res.data.share_data);
					var VIP_data = ding(res.data.heat_data);
					this.imitationData = getPercentage(share_data);
					this.imitationVIPData = getPercentage(VIP_data);
				}
			});
			this.$get('api/workOrder').then(res => {
				if (res.code == '000000') {
					var data = res.data;
					var arr = [];
					for (let i = 0; i < data.length; i++) {
						arr.push({
							WorkOrderElP: data[i].headline,
							WorkOrderDepict: data[i].describe,
							WorkOrderTime: data[i].update_time,
							status: data[i].state,
							img: data[i].picture
						});
					}
					this.WorkOrderListArr = arr;
				}
			});
		},
		setIntervalFn(echartsObj, time) {
			setInterval(() => {
				var data = echartsObj.series[0].data;
				data.shift();
				data.push(Math.random() * 20);
				echartsObj.series[0].data = data;
			}, time);
		},
		// 会员结构echarts事件
		VIPGetUpMouseover(val) {
			this.VIPGetUpCenterText = dataPercentageFn(
				'',
				this.initVIPGetUpSum,
				val.value
			);
		},
		VIPGetUpMouseenter(val) {
			this.VIPGetUpCenterText = val;
		},
		VIPGetUpClick(val, index) {
			this.initVIPGetUpLegendBl[index] = !this.initVIPGetUpLegendBl[index];
			this.$refs.VIPGetUp.dispatchAction({
				type: 'legendToggleSelect',
				name: val
			});
		},
		// 消费分析echarts事件
		VIPConsumeMouseover(val) {
			this.initVIPConsumeText = dataPercentageFn(
				'',
				this.initVIPConsumeSum,
				val.value
			);
		},
		VIPConsumeMouseenter(val) {
			this.initVIPConsumeText = val;
		},
		VIPConsumeClick(val, index) {
			this.initVIPConsumeLegendBl[index] = !this.initVIPConsumeLegendBl[index];
			this.$refs.VIPConsume.dispatchAction({
				type: 'legendToggleSelect',
				name: val
			});
		},
		VIPRankingMousemove(index, event) {
			this.tooltipText =
        this.imitationData[index - 1].name +
        ':' +
        this.imitationData[index - 1].number;
			this.winX = event.clientX;
			this.winY = event.clientY;
			this.tooltipIndex = index;
			this.itemOffsetX = event.offsetX;
		}
	},
	mounted() {
		this.initVIPConsumeText = dataPercentageFn(
			'',
			this.initVIPConsumeSum,
			this.initVIPConsume[0].num
		);
		this.VIPGetUpCenterText = dataPercentageFn(
			'',
			this.initVIPGetUpSum,
			this.initVIPGetUp[0].num
		);
		this.init();
		this.setIntervalFn(this.todayVIP, 3000);
		this.setIntervalFn(this.VIPTotal, 5000);
		this.setIntervalFn(this.todayOrder, 7000);
		this.setIntervalFn(this.orderTotal, 6500);
	},
	computed: {}
};
</script>
